/* FONT PATH
 * -------------------------- */
@font-face {
  font-family: 'CalibreMedium';
  src: url('/fonts/Calibre-Medium/Calibre-Medium.eot');
  src: url('/fonts/Calibre-Medium/Calibre-Medium.eot?#iefix') format('embedded-opentype'), url('/fonts/Calibre-Medium/Calibre-Medium.woff')
   format('woff'),  url('/fonts/Calibre-Medium/Calibre-Medium.ttf') format('truetype'), url('/fonts/Calibre-Medium/Calibre-Medium.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'CalibreSemiBold';
  src: url('/fonts/Calibre-SemiBold/Calibre-SemiBold.eot');
  src: url('/fonts/Calibre-SemiBold/Calibre-SemiBold.eot?#iefix') format('embedded-opentype'), url('/fonts/Calibre-SemiBold/Calibre-SemiBold.woff')
   format('woff'),  url('/fonts/Calibre-SemiBold/Calibre-SemiBold.ttf') format('truetype'), url('/fonts/Calibre-SemiBold/Calibre-SemiBold.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'CalibreMediumItalic';
  src: url('/fonts/Calibre-MediumItalic/Calibre-MediumItalic.eot');
  src: url('/fonts/Calibre-MediumItalic/Calibre-MediumItalic.eot?#iefix') format('embedded-opentype'), url('/fonts/Calibre-MediumItalic/Calibre-MediumItalic.woff')
   format('woff'),  url('/fonts/Calibre-MediumItalic/Calibre-MediumItalic.ttf') format('truetype'), url('/fonts/Calibre-MediumItalic/Calibre-MediumItalic.svg') format('svg');
  font-weight: normal;
