}

@font-face {
  font-family: 'CalibreRegular';
  src: url('/fonts/Calibre-Regular/Calibre-Regular.eot');
  src: url('/fonts/Calibre-Regular/Calibre-Regular.eot?#iefix') format('embedded-opentype'), url('/fonts/Calibre-Regular/Calibre-Regular.woff')
   format('woff'),  url('/fonts/Calibre-Regular/Calibre-Regular.ttf') format('truetype'), url('/fonts/Calibre-Regular/Calibre-Regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}


$primary-font: "CalibreMedium", Helvetica, Arial, sans-serif;
$secondary-font: Georgia, Times, "Times New Roman", serif;
$alt-font: Menlo, 'Bitstream Vera Sans Mono', 'DejaVu Sans Mono', Monaco, Consolas, monospace;

$base-font-size:    16px;
$base-line-height:  24px;

$h1-size:           36px;
$h2-size:           30px;
$h3-size:           24px;
$h4-size:           20px;
$h5-size:           16px;
$h6-size:           14px;

$extra-large:       64px;
$large:             48px;
$medium:            36px;
$small:             24px;
$extra-small:       16px;
